      </v-container>
      
    </v-card-text>

  </v-card>

</template>


<script>

import utils from '../../utils.js'
import { mapActions } from 'vuex';

const PowerConversionOptions = ["dBm", "dBW", "W"];

export default {
  name: 'UnitInfo',

  props: {

    Info: {
      type: Object,
      default: function() {
        return {
          Model: "Model No",
          Serial: "Serial No",
          Firmware: "Firmware Vers",
          Bootloader: "Bootloader Vers"
        }
      }
    },
    
    Alias: {
      type: String,
      default: null
    },

    PowerOnState: {
      type: Boolean,
      default: true
    },

    PowerConversion: {
      type: Number,
      default: 0
    }

  },

  computed: {
    StepAttenIncrementString() {
      return "Step Increment is " + parseFloat(this.UnitOption.Tx.StepAtten.Rez).toFixed(2) + "dB";
    },
    
    IsDifferent() {
      return this.new_StepAtten != this.Status.TxStepAtten
        || this.new_SwEnable != this.Status.SwTxEnabled
        || this.new_Alias != this.Alias;
    },

  },

  created() {
    this.PowerConversionOptions = PowerConversionOptions;
  },

  methods: {
    ...mapActions([
      "PerformSetStepAtten", 
      "PerformSetSwTxEnabled", 
      "PerformSetUnitAlias",
      "PerformSetPowerConversion",
      "PerformSetPowerOnState"
    ]),


    ConfirmRebootUnit(reboot) {
      if (reboot) {
        console.log("Rebooting Unit");
      }
      this.rebootDialog = false;
    },

  
  },

  data: function() {
    return {
      new_Alias: this.Alias,
      rebootDialog: false,

      new_BootupRf: this.PowerOnState,
      new_PowerConversion: this.PowerConversion,

    }
  },

  watch: {
    
    new_Alias: function(value) {
      utils.debounceMethod(() => {
        if (value != this.Alias) {
          this.PerformSetUnitAlias(value);
        }
      });
    },

    new_PowerConversion: function(value) {
      utils.debounceMethod(() => {
        if (value != this.PowerConversion) {
          this.PerformSetPowerConversion(value);
        }
      });
    }
  }

}
</script>

