import _ from 'lodash';

export default {

  debounceMethod: _.debounce(function(method) {
    method();
  }, 750),

  ConversionPowerUnit(power, conversion) {
    var calculatedPower = power;
    switch (conversion) {
      // dBm to dBw
      case 1:
        calculatedPower = power - 30.0;
        break;
      
      // dBm to Watt
      case 2:
        calculatedPower = (1 * Math.pow(10, power / 10)) / 1000.0;        
        break;
    }
    return calculatedPower;
  },

  ConvertBackTodBm(power, conversion) {
    power = parseFloat(power);
    var calcPower = power;
    switch (conversion) {

      // dBw t dBm
      case 1:
        calcPower = power + 30.0;
        break;

      // Watt to dBm
      case 2:
        calcPower = (10.0 * Math.log10(1000.0 * power));
        break;
    }
    return Math.round(calcPower * 100) / 100;
  },

  GetPowerUnit(conversion) {
    switch (conversion) {
      case 1:
        return "dBw";
      
      case 2:
        return "W";

      default:
        return "dBm";
    }
  },

  GetFaultDefinition(selection) {
    switch (selection) {
      case 1:
        return "Alarm";

      case 2:
        return "Fault";

      default:
        return "Off";
    }
  },

  ReadFileAsync(filename) {
    return new Promise((resolve, reject) => {
      let reader = new FileReader();

      reader.onloadend = function(e) {
        resolve(e.target.result);
      };

      reader.onerror = function(e) {
        reject(e);
      };

      reader.readAsText(filename);
    });
  },

  ConvertTo2Decimal(number) {
    return parseFloat(number).toFixed(2);
  },

  IsValidIp(addr) {
    var ipformat = /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
    if (addr.match(ipformat)) {
      return true;
    }
    return false;
  },

  CompareFaultDefinitions(faults, newFaults) {
    return faults.HighRfPower == newFaults.HighRfPower
      && faults.HighRfOption == newFaults.HighRfOption
      && faults.LowRfPower == newFaults.LowRfPower
      && faults.LowRfOption == newFaults.LowRfOption
      && faults.ReflectedPower == newFaults.ReflectedPower
      && faults.ReflectedOption == newFaults.ReflectedOption;
  },

  CompareClockNtp(ntp, newNtp) {
    var isSame = true;
    if (newNtp.IsManual) {
      isSame = newNtp.IsManual == ntp.IsManual;
    }
    else {
      isSame = ntp.PrimaryAddr == newNtp.PrimaryAddr
        && ntp.SecondaryAddr == newNtp.SecondaryAddr;
    }

    return isSame 
      && ntp.UtcOffsetMinute == newNtp.UtcOffsetMinute
      && ntp.UpdateDaily == newNtp.UpdateDaily
      && ntp.IsManual == newNtp.IsManual;
  },

  CompareAlcOptions(alcOption, newAlcOption) {
    return alcOption.SetPoint == newAlcOption.SetPoint
      && alcOption.HighAlarm == newAlcOption.HighAlarm
      && alcOption.LowAlarm == newAlcOption.LowAlarm
      && alcOption.HighFault == newAlcOption.HighFault
      && alcOption.LowFault == newAlcOption.LowFault;
  },

  CompareIpConfigurations(ipInfo, newIpInfo) {
    return ipInfo.IsDhcp == newIpInfo.IsDhcp
      && ipInfo.Address == newIpInfo.Address
      && ipInfo.Subnet == newIpInfo.Subnet;
  }

}