<template>
  <v-card>
    <v-app-bar :color="GetBannerColor" fixed dark class="pb-1">
      <v-toolbar-title>Mission Microwave</v-toolbar-title>

      <v-spacer/>
      <div class='headline d-flex align-center font-weight-bold' v-if="getUnitAlias.length > 0"> {{getUnitAlias}} </div>

      <v-spacer/>

      <v-btn color="white" class="primary--text" v-on:click="SigningOut">
          Log Out
        </v-btn>
    </v-app-bar>

    <v-container fluid class="pt-16">
    <!-- <v-sheet class="overflow-y-auto pt-16"> -->
      <v-tabs vertical class="my-tabs pr-16" v-model="NewTabSelected">

        <v-tab v-for="tabInfo in TabHeaders" :key="tabInfo.name" class="flex-start">
          <v-icon left class='text-h4'> {{tabInfo.icon}} </v-icon>
          {{tabInfo.name}}
        </v-tab>

        <!-- Status -->
        <v-tab-item>
          <unit-info 
            v-bind:Info="getUnitInfo" 
            v-bind:Alias="getUnitAlias"
            v-bind:PowerConversion="getPowerConversion"
            v-bind:PowerOnState="getPowerOnState"></unit-info>
          <status 
            v-bind:Status="getUnitStatus"
            v-bind:PowerConversion="getPowerConversion"
            v-bind:DesiredPower="getDesiredPower"></status>

          <!-- <alc-settings
            v-bind:AlcSettings="getAlcStatus"
            v-bind:PowerConversion="getPowerConversion"></alc-settings> -->
        </v-tab-item>

        <!-- Event Page -->
        <!-- <v-tab-item>
          <mission-event></mission-event>
        </v-tab-item> -->

        <!-- Logs -->
        <!-- <v-tab-item>
          <monitor
            v-bind:PowerConversion="getPowerConversion"></monitor>
        </v-tab-item> -->

        <!-- Settings -->
        <!-- <v-tab-item>
          <unit-info 
            v-bind:Info="getUnitInfo" 
            v-bind:Alias="getUnitAlias"
            v-bind:PowerConversion="getPowerConversion"
            v-bind:PowerOnState="getPowerOnState"></unit-info>
          <rf-settings 
            v-bind:PowerConversion="getPowerConversion"
            v-bind:FaultSettings="getFaultDefinitions"></rf-settings>
          <clock-ntp v-bind="getClockNtp"></clock-ntp>
          <ip-configuration v-bind="getIpInfo"></ip-configuration>
          <serial-interface v-bind="getSerialOptions"></serial-interface>
          <update-firmware></update-firmware>
          <test-fault></test-fault>
          <import-export
            v-bind:Alias="getUnitAlias"
            v-bind:PowerOnRf="getPowerOnState"
            v-bind:PowerUnit="getPowerConversion"
            v-bind:FaultDefinitions="getFaultDefinitions"
            v-bind:NtpOptions="getClockNtp"
            v-bind:SerialOptions="getSerialOptions"
            v-bind:AlcOptions="getAlcStatus"
            v-bind:IpConfigurations="getIpInfo"
            v-bind:PowerConversion="getPowerConversion"></import-export>
        </v-tab-item> -->
        <v-tab-item>
          <ip-configuration v-bind="getIpInfo"></ip-configuration>
        </v-tab-item>

        <!-- <v-tab-item>
          <snmp> </snmp>
        </v-tab-item> -->

        <!-- <v-tab-item>
          <documents> </documents>
        </v-tab-item> -->

      </v-tabs>
    <!-- </v-sheet> -->
    </v-container>

  </v-card>
</template>


<script>
import websocketPlugin from '../store/plugins/web-socket'
import { mapGetters, mapActions } from 'vuex';

// Front Page (RF Status Page)
import Status from './RfStatusPage/Status.vue'
// import AlcSettings from './RfStatusPage/AlcSettings.vue'

// // Event Page
// import MissionEvent from './EventPage/MissionEvent.vue'

// // Plot Page
// import Monitor from './PlotPage/Monitor.vue'

// // Setting Page
import UnitInfo from './SettingsPage/UnitInfo.vue'
// import RfSettings from './SettingsPage/RfSettings.vue'
// import ClockNtp from './SettingsPage/ClockNtp.vue'
import IpConfiguration from './SettingsPage/IpConfiguration.vue'
// import SerialInterface from './SettingsPage/SerialInterface.vue'
// import UpdateFirmware from './SettingsPage/UpdateFirmware.vue'
// import TestFault from './SettingsPage/TestFault.vue'
// import ImportExport from './SettingsPage/ImportExport.vue'

// // SNMP Page
// import Snmp from './SnmpPage/Snmp.vue'

// // Document Page
// import Documents from './DocumentPage/Documents.vue'

const TabHeaders = [
  {
    name: "RF Status",
    icon: "mdi-view-dashboard-outline"
  },
  // {
  //   name: "Event",
  //   icon: "mdi-calendar-blank-outline"
  // },
  // {
  //   name: "Plots",
  //   icon: "mdi-book-open-outline"
  // },
  {
    name: "Settings",
    icon: "mdi-cog"
  },
  // {
  //   name: "SNMP",
  //   icon: "mdi-lan"
  // },
  // {
  //   name: "Documents",
  //   icon: "mdi-file-document-multiple"
  // }
];

export default {
  name: 'MissionTab',

  components: {
    Status,
    // AlcSettings,

    IpConfiguration,
    // ClockNtp,
    // MissionEvent,
    // Monitor,
    // SerialInterface,
    // UpdateFirmware,
    // RfSettings,
    UnitInfo,
    // TestFault,
    // ImportExport,

    // Snmp,
    // Documents
  },

  computed: {
    ...mapGetters([
      "getUnitInfo",
      "getIpInfo", 
      "getUnitStatus", 
      "getSerialOptions",  
      "getClockNtp",

      "getAlcStatus",
      "getFaultDefinitions",
      
      "getUnitAlias",
      "getPowerOnState",
      "getPowerConversion",
      "getDesiredPower"]),

    GetBannerColor() {
      var status = this.getUnitStatus;

      if (status.Faults != null 
        && status.Faults.length == 0)
      {
        if (status.SwTxEnabled) {
          return 'green';
        }
        return 'amber';
      }
      return 'red';
    },
  },

  props: {
    UnitAlias: {
      type: String,
      default: "Javelin 2"
    }
  },

  data: function() {
    return {
      NewTabSelected: 0
    };
  },

  methods: {
    ...mapActions(["PerformLogOut"]),

    SigningOut: function() {
      sessionStorage.removeItem('credential');
      this.$router.push('/');
      this.PerformLogOut();

      const url = 'http://192.168.235.32:28080/logout';
      fetch(url, {
        method: 'POST',
        body: JSON.stringify({credential: "C0FFEE"})
      });
    }
  },

  created: function() {
    this.TabHeaders = TabHeaders;
    websocketPlugin(this.$store);
  },

}
</script>

<style>
.my-tabs [role="tab"] {
  justify-content: flex-start;
}

</style>