import CommadSet from '../../mission/commands.json';
import mission from '../../mission/misison.js';

export default function websocketPlugin(store) {
  

  function connect() {
    // var url = "ws://" + location.hostname + ":28080/websocket";
    var url = "ws://192.168.235.32:28080/websocket";

    var websocket = new WebSocket(url);

    // Received Data from Web Socket
    websocket.onmessage = function(event) {
      var dataReceived = JSON.parse(event.data);
      store.commit('PROCESS_RECEIVED_DATA', dataReceived);
    }

    // Web Socket Is Connected
    websocket.onopen = function(event) {
      console.log('On Open', event);
      store.commit('SET_WEBSOCKET_CONNECTED', true);
      if (store.getters.getTimerPromise == null) {
        var timerPromise = setInterval(timerFunction, 5000, websocket);
        store.commit('SET_PROMISE', timerPromise);
      }
    }

    // Web Socket Is Closed
    websocket.onclose = function(event) {
      console.log('On close', event);
      store.commit('SET_WEBSOCKET_CONNECTED', false);
      
      if (store.getters.getTimerPromise != null) {
        clearInterval(store.getters.getTimerPromise);
      }
    }

    websocket.onerror = function(event) {
      console.log('On error', event);

      if (store.getters.getTimerPromise != null) {
        clearInterval(store.getters.getTimerPromise);
      }
    }

    console.log(mission);
    store.commit("SET_WEBSOCKET", websocket);

    var timerFunction = function(socket) {
      var pollingState = store.getters.getPollingState;
      console.log('Timer Function Poll State', socket);
      switch (pollingState) {
        case CommadSet.CMD_GET_UNIT_INFO: {
          // console.log('Get unit info');
          try {
            var msg = mission.GetUnitInfoCommand();
            msg.key = store.getters.getSecretKey;
            console.log(msg);
            socket.send(msg);
          }
          catch (ex) {
            console.log(ex);
          }
          
          // store.commit('GET_UNIT_INFO');
          // store.commit('SET_POLLING_STATE', CommadSet.CMD_GET_IP_INFO);
        }
        break;

        case CommadSet.CMD_GET_IP_INFO: {
          store.commit('GET_IP_INFO');
          store.commit('SET_POLLING_STATE', CommadSet.CMD_GET_NTP);
        }
        break;

        case CommadSet.CMD_GET_NTP: {
          store.commit('GET_NTP_OPTIONS');
          store.commit('SET_POLLING_STATE', CommadSet.CMD_SET_POWER_CONVERSION);
        }
        break;

        case CommadSet.CMD_SET_POWER_CONVERSION: {
          store.commit('GET_POWER_CONVERSION');
          store.commit('SET_POLLING_STATE', CommadSet.CMD_GET_ALIAS);
        }
        break;

        case CommadSet.CMD_GET_ALIAS: {
          store.commit('GET_UNIT_ALIAS');
          store.commit('SET_POLLING_STATE', CommadSet.CMD_GET_POWER_ON_STATE);
        }
        break;

        case CommadSet.CMD_GET_POWER_ON_STATE: {
          store.commit('GET_POWER_ON_STATE');
          store.commit('SET_POLLING_STATE', CommadSet.CMD_SET_BAUDRATE);
        }
        break;

        case CommadSet.CMD_SET_BAUDRATE: {
          store.commit('GET_BAUDRATE_OPTIONS');
          store.commit('SET_POLLING_STATE', CommadSet.CMD_GET_UNIT_OPTIONS);
        }
        break;

        case CommadSet.CMD_GET_UNIT_OPTIONS: {
          store.commit('GET_UNIT_OPTIONS');
          store.commit('SET_POLLING_STATE', CommadSet.CMD_GET_USER_STATUS);
        }
        break;

        case CommadSet.CMD_GET_USER_STATUS: {
          store.commit('GET_USER_STATUS');
        }
        break;
      }
    }
  }

  connect();
}
